@import url('https://fonts.googleapis.com/css2?family=Inknut+Antiqua:wght@300;400;500;600;700;800;900&display=swap');

@font-face {
  font-family: OCRAE;
  src: url('../public/assets/fonts/OCRA.TTF');
}

* {
  margin: 0px;
  padding: 0px;
  color: var(--secondaryColor);
  cursor: cell;
  /* user-select: none; */
}

body {
  width: 100%;
  height: 100%;
  overflow-x: hidden;

}

.page {
  width: 100vw;
  height: 100vh;
}

::-webkit-scrollbar {
  width: 6px;
  height: 100vh;
  background-color: var(--primaryColor);
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  border: 1px solid var(--accentColor);
  background-color: var(--secondaryColor);
}

::selection {
  color: var(--primaryColor);
  background-color: var(--accentColor);
}

h1 {
  user-select: text;
  font-family: "Inknut Antiqua", "serif";
  font-size: clamp(40px, 8vw, 100px);
  line-height: 1.2;
}

h2 {
  font-family: "Inknut Antiqua", "serif";
  font-size: clamp(30px, 5vw, 80px);
  line-height: 1.2;
  width: fit-content;
}

p {
  font-family: 'OCRAE', 'Bebas Neue';
  line-height: 1.1;
}

#custom-cursor {
  position: fixed;
  height: 4em;
  width: 4em;
  transform: translate(-50%, -50%);
  transform-origin: top left;
  border-radius: 100px;
  background-color: rgba(0, 0, 0, 0);
  backdrop-filter: invert();
  pointer-events: none;
  z-index: 100;
}

.expanded {
  animation: expandAnimation 1s forwards 1;
}

@keyframes expandAnimation {
  0% {
    scale: 100%;
  }

  50% {
    scale: 150%;
  }

  100% {
    scale: 100%;
  }
}

.wave {
  animation: waveMove 4s linear 0s infinite;
}

@keyframes waveMove {
  0% {
    translate: 0px;
  }

  100% {
    translate: 1000px;
  }
}

.logo {
  fill: var(--primaryColor);
}

.logo2 {
  fill: var(--secondaryColor);
}

/* Popper tooltip glassy style */
.popper-tooltip {
  background: rgba(40, 40, 40, 0.75);
  color: var(--primaryColor, #00bcd4);
  padding: 12px 16px;
  border-radius: 10px;
  z-index: 1000;
  margin: 10px;
  box-shadow: 0 4px 24px 0 rgba(0, 0, 0, 0.18);
  backdrop-filter: blur(6px);
  border: 1px solid rgba(255, 255, 255, 0.12);
  font-weight: 600;
  font-size: 1rem;
  display: flex;
  align-items: center;
  gap: 8px;
}

/* Popper tooltip link */
.popper-tooltip a {
  font-weight: 500;
}